.main-profile-info {
    h1 {
        color: var(--Text, #211B3A);
        font-family: Urbanist;
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 28px;
        /* 116.667% */
    }

    .all-user-deatils-div {
        border-radius: 12px;
        background: #FFF;
        padding: 30px;
    }
}

.info-div {
    display: grid;
    grid-template-columns: repeat(3, 1fr);

}

.info-heading-div {
    h2 {
        color: var(--Text, #211B3A);
        font-family: Urbanist;
        font-size: 22px;
        font-style: normal;
        font-weight: 600;
        line-height: 28px;
        margin-bottom: 30px;
    }
}

.inf {
    display: flex;
    align-items: center;

    span {
        color: #000;
        font-family: Urbanist;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: 28px;
        /* 155.556% */
        width: 100px;
    }

    h5 {
        color: rgba(0, 0, 0, 0.78);
        font-family: Urbanist;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 28px;
        /* 155.556% */
        margin: 0px;
    }

    h6 {
        color: rgba(11, 56, 161, 0.78);
        font-family: Urbanist;
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: 28px;
        /* 155.556% */
        margin: 0px;
        cursor: pointer;
    }
}

.warning-box {
    display: flex;
    align-items: center;
    background-color: #fff4db;
    border-radius: 8px;
    padding: 12px;
    gap: 8px;
    color: #333;

    p {
        color: #A16800;
        font-family: Urbanist;
        font-size: 12px;
        font-style: normal;
        font-weight: 500;
        line-height: 18px;
        margin: 0;
    }
}

.form-label {
    color: var(--Text-Dark, #212121);
    font-family: Urbanist;
    font-size: var(--Font-Size-2, 14px);
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-Heights-10, 20px);
    letter-spacing: var(--Letter-Spacing-2, 0.1px);
}
.password-input {
  position: relative;

  input {
    width: 100%;
    padding-right: 40px;
  }

  .eye-button {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;

    img {
      width: 20px;
    }
  }
}